import React from 'react';
import { Link } from 'react-router-dom';

const Dashboard = () => {
  const pages = [
    { name: '', path: 'home' },
    { name: ' ', path: 'about' },
    { name: '', path: 'contact' },
    { name: '', path: 'news' },
    { name: '', path: 'schedule' },
    { name: '', path: 'teachers' }
  ];

  return (
    <div className="p-6">
      <h1 className="text-3xl font-bold mb-6">  </h1>
      
      <div className="grid" style={{ 
        display: 'grid', 
        gridTemplateColumns: 'repeat(auto-fill, minmax(300px, 1fr))', 
        gap: '1rem' 
      }}>
        {pages.map((page) => (
          <Link
            key={page.path}
            to={`/admin/editor/${page.path}`}
            className="bg-white p-6 rounded-lg shadow-md hover:shadow-lg transition-all border block no-underline text-inherit"
          >
            <h3 className="text-xl font-semibold mb-2">{page.name}</h3>
            <p className="text-gray-600">  </p>
          </Link>
        ))}
      </div>
    </div>
  );
};

export default Dashboard;